body {
  font-family: Courier New, Courier, monospace;
  font-size: medium;
  font-weight: 300;
}

.loader {
  width: 100px;
  height: 100px;
  margin-top: -50px;
  margin-left: -50px;
  position: absolute;
  top: 50%;
  left: 50%;
}

.loader:before, .loader:after {
  content: "";
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 0 0 10px 2px rgba(0, 0, 0, .3);
}

.loader:after {
  animation: rotate 2s linear infinite;
  box-shadow: inset 0 2px #ff3d00;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.preloader {
  z-index: 1001;
  background: #e2c8c8;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.cat-info {
  gap: 30px;
  margin-top: 30px;
  display: flex;
}

span {
  font-weight: bold;
}

.descr {
  width: 500px;
}

h2 {
  margin-left: 20px;
}

.error, .loaded .preloader {
  display: none;
}

.loaded .breed-select {
  display: block;
}

.breed-select {
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
  font-size: 18px;
}

